/* Import statements */
import React, { Component } from 'react';
import { Link, Route, Switch, BrowserRouter as Router } from 'react-router-dom';
import Home from './views/home';
import Contact from './views/contact';
import Projects from './views/projects';
import err from './views/e404';
import Sidebar from "react-sidebar";

import './App.css';


/* Category component */
// const Contact = () => (
//   <div>
//     <h2>Contact</h2>
//   </div>
// )


/* App component */
class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      sidebarOpen: false
    };
    this.onSetSidebarOpen = this.onSetSidebarOpen.bind(this);
  }

  onSetSidebarOpen(open) {
    this.setState({ sidebarOpen: open });
    
    document.getElementById("main-content").classList.toggle('moved');
  }


  render() {
    return (
      <Router>
      <div>
        <Sidebar
          sidebar={
            <nav className="navbar navbar-light">
              <ul className="nav navbar-nav">

              {/* Link components are used for linking to other views */}
                <Link to="/"><li>Home</li></Link>
                <Link to="/contact"><li>Contact</li></Link>
                <Link to="/projects"><li>Projects</li></Link>
              </ul>
            </nav>
          }
          open={this.state.sidebarOpen}
          onSetOpen={this.onSetSidebarOpen}
          styles={{ sidebar: { background: "#004d7ce0", width: "150px" } }}
        >

        </Sidebar>
        <div className="App-header">
          <div id="main-content">
            <div className="toggle-btn" onClick={() => this.onSetSidebarOpen(true)}><span></span><span></span><span></span></div>

            <Switch>
              <Route exact path="/" component={Home}/>
              <Route path="/contact" component={Contact}/>
              <Route path="/projects" component={Projects}/>
              <Route component={err}/>
            </Switch>
          </div>
        </div>
      </div>
      </Router>
    )
  }
}

export default App;


